.column-split {
  border-radius: 28px;
  overflow: hidden;
  border: solid 2px var(--bg-color-grey-1, #f8f8f8);
  background: var(--bg-color-grey-1, #f8f8f8);
}
.column-split:global(.read-only) :global(.ant-splitter-bar-dragger), .column-split:global(.disabled) :global(.ant-splitter-bar-dragger) {
  display: none;
}
.column-split:global(.disabled) {
  cursor: not-allowed;
  opacity: 0.5;
}
.column-split:not(:global(.read-only)):not(:global(.disabled)):hover .column-item-handler-left,
.column-split:not(:global(.read-only)):not(:global(.disabled)):hover .column-item-handler-right {
  opacity: 1;
}
.column-split :global .ant-splitter-bar-dragger {
  opacity: 0;
}

.column-item:global(.ant-splitter-panel) {
  padding: 0;
}

.column-item-content {
  height: 100%;
  width: 100%;
  position: relative;
  color: var(--color, var(--font-color, "#222"));
  border-radius: 2px;
  padding: 4px 0;
  white-space: nowrap;
  overflow: hidden;
}
.column-item-content:before {
  pointer-events: none;
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: var(--color, #f8f8f8);
  opacity: 0.3;
  position: absolute;
}

.column-item-handler-left,
.column-item-handler-right {
  position: absolute;
  transform: translate(0, -50%);
  color: #fff;
  background: var(--primary-color);
  height: 24px;
  width: 12px;
  border-radius: 12px;
  pointer-events: none;
  opacity: 0;
  transition: opacity 300ms;
}

.column-item-handler-left {
  top: 50%;
  right: -6px;
}
.column-item-handler-left :global .anticon {
  transform: translateX(-3px);
}

.column-item-handler-right {
  top: 50%;
  left: -6px;
}
.column-item-handler-right :global .anticon {
  transform: translateX(1px);
}